<template>
    <div class="firstpage">
        <div class="f-header">
            <el-row :gutter="10">
                <el-col :span="6">
                    <el-card :body-style="{ padding: '0px' }">
                        <div class="fh-header">
                            <div>
                                <p class="num txt-blue">{{ basicPeopleStatis.total }}</p>
                                <p class="txt">人口总数</p>
                            </div>
                            <div>
                                <img :src="img.people" alt="">
                            </div>
                        </div>
                        <div class="fh-bottom bg-gray">
                            <span>常住人口占比：{{ ((basicPeopleStatis.housePeople/basicPeopleStatis.total) * 100).toFixed(2) }}%</span>
                            <div>
                                <img :src="img.ratio" alt="">
                            </div>
                        </div>
                    </el-card>
                </el-col>
                <el-col :span="6">
                    <el-card :body-style="{ padding: '0px' }">
                        <div class="fh-header">
                            <div>
                                <p class="num txt-red">{{ homeInfoStatis.total }}</p>
                                <p class="txt">房屋总数</p>
                            </div>
                            <div>
                                <img :src="img.house" alt="">
                            </div>
                        </div>
                        <div class="fh-bottom bg-green">
                            <span>房屋入住率：{{ ((homeInfoStatis.inHouse/homeInfoStatis.total) * 100).toFixed(2) }}%</span>
                            <div>
                                <img :src="img.ratio" alt="">
                            </div>
                        </div>
                    </el-card>
                </el-col>
                <el-col :span="6">
                    <el-card :body-style="{ padding: '0px' }">
                        <div class="fh-header">
                            <div>
                                <p class="num txt-green">{{deal.report}}</p>
                                <p class="txt">上报案件数</p>
                            </div>
                            <div>
                                <img :src="img.report" alt="">
                            </div>
                        </div>
                        <div class="fh-bottom bg-violet">
                            <span>案件处置率：{{deal.rate}}%</span>
                            <div>
                                <img :src="img.ratio" alt="">
                            </div>
                        </div>
                    </el-card>
                </el-col>
                <el-col :span="6">
                    <el-card :body-style="{ padding: '0px' }">
                        <div class="fh-header">
                            <div>
                                <p class="num txt-orange">{{todayDeal.report}}</p>
                                <p class="txt">今日上报数</p>
                            </div>
                            <div>
                                <img :src="img.newReport" alt="">
                            </div>
                        </div>
                        <div class="fh-bottom bg-orange">
                            <span>案件处置率：{{todayDeal.rate}}%</span>
                            <div>
                                <img :src="img.ratio" alt="">
                            </div>
                        </div>
                    </el-card>
                </el-col>
            </el-row>
        </div>
        <div class="main">
            <el-row style="height:100%;">
                <el-col :span="7" style="height: 100%;">
                    <el-row class="fm-charts">
                        <el-col :span="24" style="height: 100%;">
                            <techo-card resizeEvent="triDealStats">
                                <div slot="header">各区人口统计</div>
                                <div id="PeopleStatis" style="height:100%"></div>
                            </techo-card>
                        </el-col>
                    </el-row>
                    <el-row class="fm-charts" style="padding-top: 0.66rem;">
                        <el-col :span="24" style="height: 100%;">
                            <techo-card>
                                <div slot="header">重点人群</div>
                                <div id="KeyPeopleStatis" style="height:100%"></div>
                            </techo-card>
                        </el-col>
                    </el-row>
                </el-col>
                <el-col :span="11" style="padding:0 0.66rem;height: 100%;">
                    <el-row class="fm-region">
                        <el-col :span="24" style="height: 100%;">
                            <techo-card>
                                <div slot="header">系统登录人数趋势</div>
                                <div id="OnLineNum" style="height:100%"></div>
                            </techo-card>
                        </el-col>
                    </el-row>
                    <el-row class="fm-organization" style="margin: 0.36rem 0;">
                        <el-col :span="24" style="height: 100%;">
                            <techo-card>
                                <div slot="header">两新组织</div>
                                <div class="fm-content">
                                    <el-row style="height: 100%;">
                                        <el-col :span="6" style="height: 100%;">
                                            <div class="img">
                                                <img :src="img.government" alt="">
                                            </div>
                                            <div class="fm-l-content">
                                                <span class="name">社会组织</span>
                                                <span class="num txt-orange">{{organStatis.social}}家</span>
                                            </div>
                                            <div class="fm-l-content">    
                                                <span class="name">非公经济组织</span>
                                                <span class="num txt-blue">{{organStatis.unpublicEco}}家</span>
                                            </div>
                                        </el-col>
                                        <el-col :span="18" style="height: 100%;">
                                            <div id="OrganStatis" style="height:100%"></div>
                                        </el-col>
                                    </el-row>
                                </div>
                            </techo-card>
                        </el-col>
                    </el-row>
                    <el-row class="fm-notice">
                        <el-col :span="24" style="height: 100%;">
                            <techo-card>
                                <div slot="header">通知公告</div>
                                <div class="fm-content">
                                    <vue-seamless-scroll :data="noticeListData" class="seamless-warp" :class-option="noticeOption">
                                        <div class="fm-list" v-for="(item,index) in noticeListData" :keys="index">
                                            <div>{{item.publishTime}}</div>
                                            <div class="notice-title" :title="item.title">{{item.title}}</div>
                                        </div>
                                    </vue-seamless-scroll>
                                </div>
                            </techo-card>
                        </el-col>
                    </el-row>
                </el-col>
                <el-col :span="6" style="height: 100%;">
                    <el-row class="fm-charts">
                        <el-col :span="24" style="height: 100%;">
                            <techo-card>
                                <div slot="header">案卷统计</div>
                                <div slot="actions">
                                    <el-button-group>
                                        <el-button :type="buttonGroupType ? '' : 'primary'" size="mini" @click="changeType(1)">来源</el-button>
                                        <el-button :type="buttonGroupType ? 'primary' : ''" size="mini" @click="changeType(2)">类型</el-button>
                                    </el-button-group>
                                </div>
                                <div id="PeopleCommunityStatis" style="height:100%"></div>
                            </techo-card>
                        </el-col>
                    </el-row>
                    <el-row class="fm-file-statis">
                        <el-col :span="24" style="height: 100%;">
                            <techo-card>
                                <div slot="header">各社区案卷统计情况</div>
                                <div id="communityFileStatis" style="height:100%"></div>
                            </techo-card>
                        </el-col>
                    </el-row>
                </el-col>
            </el-row>
        </div>
    </div>
</template>
<script>
import vueSeamlessScroll from "vue-seamless-scroll";
import barOptions from "./config/barOptions.js";
import graphOptions from "./config/graphOptions.js";
import pieOptions from "./config/pieOptions.js";
import pieOptions2 from "./config/pieOptions2.js";
import lineOptions from "./config/lineOptions.js";
import lineOptions2 from "./config/lineOptions2.js";
import service from "./service.js";
export default {
  components: {
    vueSeamlessScroll,
  },
  data() {
    return {
      img: {
        people: require("./images/people.png"),
        house: require("./images/house.png"),
        report: require("./images/report.png"),
        newReport: require("./images/reportNumber.png"),
        ratio: require("./images/ratio.png"),
        government: require("./images/government.png")
      },
      DISCODE: "6102",
      basicPeopleStatis: {}, // 人口统计
      homeInfoStatis: {}, // 房屋统计
      organStatis: {
        social: "",
        unpublicEco: ""
      }, // 两新组织

      // 系统在线人数
      onLineNumOptions: _.cloneDeep(lineOptions2),
      onLineNumChart: null,

      // 社区人口统计
      PeopleStatisOptions: _.cloneDeep(barOptions),
      PeopleStatisChart: null,
      // 重点人群
      KeyPeopleStatisOptions: _.cloneDeep(graphOptions),
      KeyPeopleStatisChart: null,
      // 两新组织
      OrganStatisOptions: _.cloneDeep(pieOptions),
      OrganStatisChart: null,
      // 案卷占比
      PeopleCommunityStatisOptions: _.cloneDeep(pieOptions2),
      PeopleCommunityStatisChart: null,
      // 案卷统计
      communityFileStatisOptions: _.cloneDeep(lineOptions),
      communityFileStatiChart: null,

      noticeListData: [{
          title: "李建平：第31位荣登“中国好人榜”的铜川人",
          publishTime: "2018-09-28",
      }, {
          title: "铜川中院推进司法审判领域“供给侧结构性改革”",
          publishTime: "2018-09-27",
      }, {
          title: "市委政法委举办 全市政法系统新进政法干警培训班",
          publishTime: "2018-09-27",
      }, {
          title: "省法院院长李智到铜川市法院调研工作",
          publishTime: "2018-08-21",
      }, {
          title: "铜川市公安局“五个狠抓”深入推进夏季社会治安整治行动",
          publishTime: "2018-08-08",
      }, {
          title: "印台公安分局三里洞派出所召开社会维稳及矛盾纠纷排查化解工作会",
          publishTime: "2018-07-19",
      }, {
          title: "我市开展“知路、爱路、护路” 宣传教育活动",
          publishTime: "2018-05-24",
      },],

      // 案卷上报滚动
      noticeOption: {
        step: 0.5
      },
      buttonGroupType: false,
      // 今日上报
      todayDeal: {
        report: 132,
        rate: 88
      },
      deal: {
        report: 532,
        rate: 98
      }
    };
  },
  mounted() {
    let that = this;
    // 系统在线人数
    this.onLineNumChart = this.$echarts.init(
      this.$el.querySelector("#OnLineNum")
    );
    // 各社区人口统计
    this.PeopleStatisChart = this.$echarts.init(
      this.$el.querySelector("#PeopleStatis")
    );
    // 重点人群
    this.KeyPeopleStatisChart = this.$echarts.init(
      this.$el.querySelector("#KeyPeopleStatis")
    );
    // 两新组织
    this.OrganStatisChart = this.$echarts.init(
      this.$el.querySelector("#OrganStatis")
    );
    // 案卷占比
    this.PeopleCommunityStatisChart = this.$echarts.init(
      this.$el.querySelector("#PeopleCommunityStatis")
    );
    // 案卷统计
    this.communityFileStatiChart = this.$echarts.init(
      this.$el.querySelector("#communityFileStatis")
    );
    // 初始化统计图自适应函数
    this.$bus.$on("triDealStats", () => {
      that.onLineNumChart.resize();
      that.PeopleStatisChart.resize();
      that.KeyPeopleStatisChart.resize();
      that.OrganStatisChart.resize();
      that.PeopleCommunityStatisChart.resize();
      that.communityFileStatiChart.resize();
    });

    /** todo 系统在线人数 */
    that.onLineNumChart.setOption(that.onLineNumOptions);
    that.onLineNumChart.resize();
    /** todo 案卷统计 */
    that.PeopleCommunityStatisChart.setOption(that.PeopleCommunityStatisOptions);
    that.PeopleCommunityStatisChart.resize();
    /** todo 社区统计 */
    that.communityFileStatiChart.setOption(that.communityFileStatisOptions);
    that.communityFileStatiChart.resize();
  },
  created() {
    /** 查询人口数据 */
    this.getPeopleData();
    /** 查询房屋数据 */
    this.getHouseData();
    /** 查询重点人数据 */
    this.getFocusPeople();
    /** 两新组织 */
    this.getDoubleOrg();
    // this.getTodayDeal();
    // this.getDeal();
    // this.getKeyArea();
    // this.getNotice();
  },
  methods: {
    // 案卷统计类型切换
    changeType(val) {
      if (val == 1) {
        this.buttonGroupType = false;
        // 案件来源统计
        this.getSource();
      } else {
        this.buttonGroupType = true;
        // 案卷类型统计
        this.getType();
      }
    },
    // 获取人口数据
    getPeopleData() {
      let that = this;
      service.requestByEntityName(
        {
          districtCode: this.DISCODE,
          entityName: "BasePeople,ViewHousePeople,ViewFlowPeople"
        },
        data => {
          this.basicPeopleStatis = {
            total: _.sumBy(_.groupBy(data, "key").BasePeople, "count"),
            housePeople: _.sumBy(
              _.groupBy(data, "key").ViewHousePeople,
              "count"
            )
          };
          that.PeopleStatisOptions.xAxis.data = _.keys(_.groupBy(data, "name"));
          that.PeopleStatisOptions.series[0].data = _.map(
            _.groupBy(data, "key").ViewHousePeople,
            "count"
          );
          that.PeopleStatisOptions.series[1].data = _.map(
            _.groupBy(data, "key").ViewFlowPeople,
            "count"
          );
          if (_.keys(_.groupBy(data, "name")).length > 10) {
            that.PeopleStatisOptions.dataZoom[0].end = 20;
          } else {
            that.PeopleStatisOptions.dataZoom[0].end = 100;
          }
          that.PeopleStatisChart.setOption(that.PeopleStatisOptions);
          that.PeopleStatisChart.resize();
        }
      );
    },
    // 获取房屋数据
    getHouseData() {
      /** 查询房屋总数 */
      service.requestByEntityName(
        {
          districtCode: this.DISCODE,
          entityName: "BuildingHouse"
        },
        data => {
          /** 查询入住数 */
          service.requestInHouse(this.DISCODE, num => {
            this.homeInfoStatis = {
              total: _.sumBy(_.groupBy(data, "key").BuildingHouse, "count"),
              inHouse: num
            };
          });
        }
      );
    },
    // 重点人口数据
    getFocusPeople() {
      let that = this;
      service.requestByEntityName(
        {
          districtCode: this.DISCODE,
          entityName:
            "ViewReleasePeople,ViewRearPeople,ViewDrugsPeople,ViewAidsPeople,ViewVitalYoungPeople,ViewWreckerPeople,ViewComCorPeople"
        }, // 刑满释放; 留守人员; 吸毒人员; 艾滋病; 重点青少年; 肇事肇祸; 矫正人员;
        data => {
          that.KeyPeopleStatisOptions.series[0].data[0].name =
            "重点人群\n " + _.sumBy(data, "count") + "人";
          that.KeyPeopleStatisOptions.series[0].data[1].name =
            "刑满释放\n " +
            _.sumBy(_.groupBy(data, "key").ViewReleasePeople, "count") +
            "人";
          that.KeyPeopleStatisOptions.series[0].data[2].name =
            "留守人员\n " +
            _.sumBy(_.groupBy(data, "key").ViewRearPeople, "count") +
            "人";
          that.KeyPeopleStatisOptions.series[0].data[3].name =
            "吸毒人员\n " +
            _.sumBy(_.groupBy(data, "key").ViewDrugsPeople, "count") +
            "人";
          that.KeyPeopleStatisOptions.series[0].data[4].name =
            "艾滋病...\n " +
            _.sumBy(_.groupBy(data, "key").ViewAidsPeople, "count") +
            "人";
          that.KeyPeopleStatisOptions.series[0].data[5].name =
            "重点青少年\n " +
            _.sumBy(_.groupBy(data, "key").ViewVitalYoungPeople, "count") +
            "人";
          that.KeyPeopleStatisOptions.series[0].data[6].name =
            "肇事肇祸...\n " +
            _.sumBy(_.groupBy(data, "key").ViewWreckerPeople, "count") +
            "人";
          that.KeyPeopleStatisOptions.series[0].data[7].name =
            "矫正人员\n " +
            _.sumBy(_.groupBy(data, "key").ViewComCorPeople, "count") +
            "人";
          that.KeyPeopleStatisChart.setOption(that.KeyPeopleStatisOptions);
          that.KeyPeopleStatisChart.resize();
        }
      );
    },
    // 两新组织统计
    getDoubleOrg() {
      let that = this;
      /** 查询两新组织数 */
      service.requestByEntityName(
        {
          districtCode: this.DISCODE,
          entityName: "ViewGroupSocial,ViewGroupUnpublicEco"
        }, // 社会组织; 非公有制经济组织
        data => {
          this.organStatis = {
            social: _.sumBy(_.groupBy(data, "key").GroupSocial, "count"),
            unpublicEco: _.sumBy(
              _.groupBy(data, "key").GroupUnpublicEco,
              "count"
            )
          };
        }
      );
      new Promise((resolve, reject) => {
        let count = 0;
        /** 查询社会组织类型 */
        service.requestByFieldName(
          {
            districtCode: this.DISCODE,
            entityName: "ViewGroupSocial",
            fieldName: "socialType"
          },
          data => {
            _.map(data, item => {
              that.OrganStatisOptions.series[0].data.push({
                name: item.name + "\n" + item.count + "家",
                value: item.count
              });
            });
            count++;
            count == 2 && resolve();
          }
        );
        /** 查询新经济组织规模 */
        service.requestByFieldName(
          {
            districtCode: this.DISCODE,
            entityName: "ViewGroupUnpublicEco",
            fieldName: "companyCategory"
          },
          data => {
            _.map(data, item => {
              that.OrganStatisOptions.series[1].data.push({
                name: item.name + "\n" + item.count + "家",
                value: item.count
              });
            });
            count++;
            count == 2 && resolve();
          }
        );
      }).then(() => {
        that.OrganStatisChart.setOption(that.OrganStatisOptions);
        that.OrganStatisChart.resize();
      });
    },
    // 上报案件数
    getDeal() {
      let that = this;
      let sTime = utilsOper.formatDate(
          new Date().getFullYear() + "-01-01" + " " + "00:00:00"
        ),
        eTime = utilsOper.formatDate(
          new Date().getFullYear() + "-12-31" + " " + "23:59:59"
        );
      // 今日上报数
      let reportNum =
        "/statis/lately/report/all/time?sTime=" + sTime + "&eTime=" + eTime;
      // 今日处置数
      let dealNum =
        "/statis/lately/deal/all/time?sTime=" + sTime + "&eTime=" + eTime;

      let getFunc = [
        service.ajaxOper.ajaxGetFunc(reportNum),
        service.ajaxOper.ajaxGetFunc(dealNum)
      ];
      service.ajaxOper.ajaxAll({
        getFunc,
        successFunc: data => {
          if (data[0].code == 200) {
            if (Object.keys(data[0].data).length < 1) {
              that.deal.report = 0;
            } else {
              let sum = 0;
              _.map(Object.values(data[0].data), item => {
                sum += item;
              });
              that.deal.report = sum;
            }
          }
          if (data[1].code == 200) {
            if (Object.keys(data[1].data).length < 1) {
              that.deal.rate = 0;
            } else {
              let rateNum = 0;
              _.map(Object.values(data[1].data), item => {
                rateNum += item;
              });
              if (that.deal.rate) {
                that.deal.rate = (rateNum / that.deal.report * 100).toFixed();
              } else {
                that.deal.rate = 0;
              }
            }
          }
        }
      });
    },
    // 今日上报数
    getTodayDeal() {
      let that = this;
      let Time =
        new Date().getFullYear() +
        "-" +
        String(
          new Date().getMonth() + 1 < 10
            ? "0" + (new Date().getMonth() + 1)
            : new Date().getMonth() + 1
        ) +
        "-" +
        String(
          new Date().getDate() < 10
            ? "0" + new Date().getDate()
            : new Date().getDate()
        );
      let sTime = utilsOper.formatDate(Time + " " + "00:00:00"),
        eTime = utilsOper.formatDate(Time + " " + "23:59:59");
      // 今日上报数
      let reportNum =
        "/statis/lately/report/all/time?sTime=" + sTime + "&eTime=" + eTime;
      // 今日处置数
      let dealNum =
        "/statis/lately/deal/all/time?sTime=" + sTime + "&eTime=" + eTime;

      let getFunc = [
        service.ajaxOper.ajaxGetFunc(reportNum),
        service.ajaxOper.ajaxGetFunc(dealNum)
      ];
      service.ajaxOper.ajaxAll({
        getFunc,
        successFunc: data => {
          if (data[0].code == 200) {
            if (Object.keys(data[0].data).length < 1) {
              that.todayDeal.report = 0;
            } else {
              let sum = 0;
              _.map(Object.values(data[0].data), item => {
                sum += item;
              });
              that.todayDeal.report = sum;
            }
          }
          if (data[1].code == 200) {
            if (Object.keys(data[1].data).length < 1) {
              that.todayDeal.rate = 0;
            } else {
              let rateNum = 0;
              _.map(Object.values(data[1].data), item => {
                rateNum += item;
              });
              if (that.todayDeal.rate) {
                that.todayDeal.rate = (
                  rateNum /
                  that.deal.report *
                  100
                ).toFixed();
              } else {
                that.todayDeal.rate = 0;
              }
            }
          }
        }
      });
    },
    // 案件来源统计
    getSource() {
      let that = this;
      let sTime = utilsOper.formatDate(
          new Date().getFullYear() + "-01-01" + " " + "00:00:00"
        ),
        eTime = utilsOper.formatDate(
          new Date().getFullYear() + "-12-31" + " " + "23:59:59"
        );
      service.service.statis({
        url: "/statis/source/allByTime?sTime=" + sTime + "&eTime=" + eTime,
        successFunc: data => {
          if (data.code == 200) {
            that.PeopleCommunityStatisOptions.legend.data = _.map(
              data.data,
              "name"
            );
            that.PeopleCommunityStatisOptions.title[0].text = "来源";
            that.PeopleCommunityStatisOptions.series[0].name = "案卷来源";
            that.PeopleCommunityStatisOptions.series[0].data = _.map(
              data.data,
              item => {
                return { value: item.count, name: item.name };
              }
            );

            that.PeopleCommunityStatisChart.setOption(
              that.PeopleCommunityStatisOptions
            );
            that.PeopleCommunityStatisChart.resize();
          }
        }
      });
    },
    // 案卷类型统计
    getType() {
      let that = this;
      let sTime = utilsOper.formatDate(
          new Date().getFullYear() + "-01-01" + " " + "00:00:00"
        ),
        eTime = utilsOper.formatDate(
          new Date().getFullYear() + "-12-31" + " " + "23:59:59"
        );
      service.service.statis({
        url: "/statis/classtype/first/time?sTime=" + sTime + "&eTime=" + eTime,
        successFunc: data => {
          if (data.code == 200) {
            that.PeopleCommunityStatisOptions.legend.data = _.map(
              data.data,
              "name"
            );
            that.PeopleCommunityStatisOptions.title[0].text = "类型";
            that.PeopleCommunityStatisOptions.series[0].name = "案卷类型";
            that.PeopleCommunityStatisOptions.series[0].data = _.map(
              data.data,
              item => {
                return { value: item.count, name: item.name };
              }
            );
            that.PeopleCommunityStatisChart.setOption(
              that.PeopleCommunityStatisOptions
            );
            that.PeopleCommunityStatisChart.resize();
          }
        }
      });
    },
    // 重点地区统计
    getKeyArea() {
    debugger
    },
    // 通知公告
    getNotice() {
      debugger
    }
  }
};
