export default {
    tooltip: {
        trigger: 'axis',
        axisPointer: {            // 坐标轴指示器，坐标轴触发有效
            type: 'shadow'        // 默认为直线，可选为：'line' | 'shadow'
        }
    },
    legend: {
        data: ['户籍人口', '流动人口']
    },
    grid: {
        left: '2%',
        right: '3%',
        bottom: '4%',
        containLabel: true
    },
    yAxis: {
        type: 'value',
        name: "人数",
        axisLabel: {
            formatter: '{value} 人'
        }
    },
    xAxis: {
        type: 'category',
        data: []
    },
    series: [
        {
            name: '户籍人口',
            type: 'bar',
            // stack: '总量',
            label: {
                normal: {
                    show: true,
                    position: 'inside',
                    formatter: "{c}人"
                },
            },
            itemStyle: {
                normal: {
                    color: "#ab5fba",
                }
            },
            data: []
        },
        {
            name: '流动人口',
            type: 'bar',
            // stack: '总量',
            label: {
                normal: {
                    show: true,
                    position: 'inside',
                    formatter: "{c}人"
                }
            },
            itemStyle: {
                normal: {
                    color: "#8aa7de",
                }
            },
            data: []
        }],
    dataZoom: [
        {
            type: "slider",
            show: true,
            start: 0,
            end: 100
        },
        {
            type: "inside",
            xAxisIndex: 0,
            zoomOnMouseWheel: true
        }
    ],
}