import request from '@/plugin/axios'

const service = {
    /**
     * 根据实体类名统计
     * 
     * @param {any} param 统计参数 districtCode 区划代码 entityName 实体类名
     * @param {any} successFunc 回调函数
     */
    requestByEntityName(param, successFunc) {
        request({
            url: "/compre/statis/common/district?districtCode="+ param.districtCode +"&entityNames=" + param.entityName,
            method: "get",
        }).then((data) => {
            successFunc(data);
        })
    },

    /**
     * 入住户数查询
     * 
     * @param {any} disCode 行政区ID
     * @param {any} successFunc 回调函数
     */
    requestInHouse(disCode, successFunc) {
        request({
            url: "/compre/hp/house/statis/have/people?districtCode="+ disCode,
            method: "get",
        }).then((data) => {
            successFunc(data);
        })
    },

    /**
     * 根据字段属性占比查询
     * 
     * @param {any} param 查询参数
     * @param {any} successFunc 回调函数
     */
    requestByFieldName(param, successFunc) {
        request({
            url: "/compre/statis/common/field?districtCode="+ param.districtCode +"&entityName="+ param.entityName+"&fieldName="+ param.fieldName,
            method: "get",
        }).then((data) => {
            successFunc(data);
        })
    }
}
export default service;
